.SearchInput {
  width: 100%;
  height: 30px; }

.SearchForm {
  width: 100%; }

.cti__root {
  width: 100% !important; }

.cti__input {
  height: 48px;
  line-height: 1.5;
  border: none; }

.cti__input__input {
  height: 100%; }

.SearchButton {
  width: 100%;
  background-color: #00cbe2;
  border-radius: 0 100px 100px 0;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  color: #ffffff;
  border: 1px solid #00cbe2;
  outline: none;
  cursor: pointer; }

.SearchSubPlaceholder {
  color: white;
  opacity: 0.75;
  font-size: 12px;
  padding-top: 10px;
  margin-bottom: 20px; }

.SearchIconContainer {
  background-color: white;
  border-radius: 24px 0 0 24px;
  padding-top: 4px;
  border: 1px solid #00cbe2;
  border-right: none; }

@media all and (max-width: 960px) {
  .SearchButton {
    height: 50px;
    border-radius: 0 0 4px 4px; }
  .SearchPageContainer .SearchInputContainer {
    height: calc(100vh - 60px); }
  .SearchPageContainer .Select-menu-outer {
    height: calc(100vh - 100px) !important;
    max-height: none !important;
    top: 50px; }
    .SearchPageContainer .Select-menu-outer .Select-menu {
      max-height: 100%; } }
